.loading {
  position: absolute;
  width: 0px;
  height: 0px;
  border: 38px solid rgba(255, 255, 255, 0.1);
  border-left: 38px solid #05fbff;
  border-right: 38px solid #05fbff;
  border-radius: 100%;
  margin: auto;
  -webkit-animation: isLoading 1s linear infinite;
          animation: isLoading 1s linear infinite;
  -webkit-transition: opacity 400ms;
  -o-transition: opacity 400ms;
  transition: opacity 400ms;
  left: calc(50% - 38px);
  top: calc(50% - 38px); }

@-webkit-keyframes isLoading {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes isLoading {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@-webkit-keyframes isLoadingBack {
  0% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); }
  100% {
    -webkit-transform: rotate(0);
            transform: rotate(0); } }

@keyframes isLoadingBack {
  0% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); }
  100% {
    -webkit-transform: rotate(0);
            transform: rotate(0); } }



