.background-wrapper {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: -2; }
  .background-wrapper .background {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -2;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    -webkit-transition: 400ms;
    -o-transition: 400ms;
    transition: 400ms; }



